
























































































































import { Component, Vue } from 'vue-property-decorator';
import Spreadsheet from '../../components/xspreadsheet';
import testEditor from './component/editTest.vue';
import createDialog from './component/createDialog.vue';
import parameView from './component/parameView.vue';
import { statementChange } from './component/conditional.js';
import treeTableList from './component/treeTableList.vue';
import {
  linkList,
  addCombin,
  viewDataEdit,
  dataViewAdd,
  dataViewDetail,
  excelInfoQuery
} from '@api/viewData';

@Component({
  components: { parameView, createDialog, testEditor, treeTableList }
})
export default class FileType extends Vue {
  private fileTypeForm: any = {
    viewName: '',
    linkId: '',
    path: '',
    sheetName: '',
    titleLine: '',
    startLine: '',
    endLine: '',
    dictFlag: 1
  };
  private rules: any = {
    viewName: [{ required: true, message: '', trigger: 'blur' }],
    linkId: [
      { required: true, message: '', trigger: 'change' }
    ],
    path: [{ required: true, message: '', trigger: 'blur' }],
    sheetName: [{ required: true, message: 'sheet', trigger: 'blur' }]
  };
  private linkList: Array<any> = [];
  private parameterTable: Array<any> = [
    {
      id: 1,
      code: '',
      name: '',
      type: '',
      required: '',
      defaultValue: '',
      addButton: true,
      edits: [0, 1, 1, 1, 1, 1, 0]
    }
  ];
  private isCompatible: any = '';
  private fieldTable: Array<any> = [];
  private testloading = false;
  private paramsActions: Array<any> = [
    {
      title: '',
      icon: 'stack-line',
      onClick: this.testQueryButton
    }
  ];

  private sheet: any = null;
  private updateId: any = '';
  private testList: Array<any> = [];
  private cellLocation: any = {};
  private cellList: Array<any> = [];
  private excelData: any = {
    styles: [
      {
        bgcolor: '#f4f5f8',
        textwrap: true,
        color: '#900b09',
        border: {
          // top: ['thin', '#000000'],
          // bottom: ['thin', '#000000'],
          right: ['thin', '#000000'],
          left: ['thin', '#0366d6']
        }
      },
      {
        textwrap: false,
        bgcolor: '#366092',
        color: '#fff',
        align: 'center',
        width: 200
      },
      {
        textwrap: false,
        bgcolor: '#c5d9f1',
        color: '#000',
        align: 'center',
        width: 200
      },
      {
        bgcolor: '#ffffff',
        color: '#000',
        align: 'center'
      },
      {
        bgcolor: '#dce6f1',
        color: '#000',
        align: 'center'
      }
    ],
    rows: {
      0: {
        cells: {
          0: { text: 'test1' },
          1: { text: 'test2' }
        }
      },
      1: {
        cells: {
          0: { text: 'rows2-test1' },
          1: { text: 'rows3-test2' }
        }
      }
    }
  };

  private dialogVisible = false;
  private actionsList: Array<any> = [
    {
      icon: '',
      type: 'primary',
      onClick: this.addSure,
      title: ''
    },
    {
      icon: '',
      type: '',
      onClick: this.addCancel,
      title: ''
    }
  ];
  private testParameterDialog = false;
  private testParameterRules: any = {};
  private testParameterList: Array<any> = [];

  // 
  created() {
    this.linkQuery();
    const id = this.$route.query.id;
    if (id) {
      this.dataViewDetail();
    }
  }
  // excel
  initExcel() {
    this.sheet = new Spreadsheet('#spreadsheet-execl', {
      view: {
        width: () => 1000,
        height: () => 240
      },
      rows: {
        len: 10,
        height: 20
      },
      col: {
        len: 26,
        width: 100,
        indexWidth: 60,
        minWidth: 50
      }
    }).loadData(this.excelData);
  }
  // 
  linkQuery() {
    linkList({
      page: 1,
      limit: 10000,
      type: '2'
    }).then(({ data }: any) => {
      this.linkList = data.rows;
    });
  }
  // 
  testQueryButton() {
    if (!this.fileTypeForm.linkId) {
      this.$message.error('');
      return false;
    }
    if (!this.fileTypeForm.path) {
      this.$message.error('');
      return false;
    }
    if (!this.fileTypeForm.sheetName) {
      this.$message.error('sheet');
      return false;
    }
    if (this.parameterTable.length > 0 && this.parameterTable[0].code) {
      this.testParameterDialog = true;
    } else {
      const params = {
        ...this.fileTypeForm,
        filePath: this.fileTypeForm.path
      };
      this.queryExcelInfo(params);
    }
  }
  changeValue(val: any, item: any) {
    this.testParameterList.forEach((listItem) => {
      if (listItem.code === item.code) {
        if (listItem.required === '1' && listItem.value === '') {
          listItem.showMsg = true;
        } else {
          listItem.showMsg = false;
        }
      }
    });
  }
  //  - 
  handleTestQuery() {
    this.testloading = true;
    let spreadQuery: Array<any> = [];
    if (this.$refs.advance['spread'].model) {
      for (const i in this.$refs.advance['spread'].model) {
        spreadQuery.push({
          code: i,
          value: this.$refs.advance['spread'].model[i]
        });
      }
    } else {
      spreadQuery = [];
    }
    const params = {
      ...this.fileTypeForm,
      filePath: this.fileTypeForm.path,
      queryParams: spreadQuery
    };
    this.queryExcelInfo(params);
  }
  queryExcelInfo(params) {
    this.testloading = true;
    excelInfoQuery(params).then((res) => {
      this.testloading = false;
      this.testParameterDialog = false;
      if (res.data) {
        this.excelData = res.data;
        const a = document.getElementById('spreadsheet-execl');
        if (a !== null) {
          a.innerHTML = '';
        }
        this.initExcel();
        this.$message.success('');
      } else {
        this.$message.error('' + res.error.message);
      }
    });
  }
  //  - 
  testParameterCancel() {
    this.$refs.advance['spread'].model = '';
    this.testParameterDialog = false;
  }
  //  
  createLink() {
    this.dialogVisible = true;
  }
  //  
  handelSure(params) {
    addCombin(params).then((res) => {
      if (res.data) {
        this.fileTypeForm.linkId = res.data.linkId;
        this.$message.success('');
        this.dialogVisible = false;
        this.linkQuery();
      } else {
        this.$message.error('' + res.error.message);
      }
    });
  }
  //  
  handelCancel() {
    this.dialogVisible = false;
  }
  //  
  addCancel() {
    const fileTypeForm: any = this.$refs.fileTypeForm;
    fileTypeForm.resetFields();
    this.$router.push('/sysSupport/dataService/viewDateManagent');
  }
  // 
  dataViewDetail() {
    this.isCompatible = this.$route.query.isCompatible;
    this.updateId = this.$route.query.id;
    const id = this.$route.query.id;
    dataViewDetail(id).then(({ data }: any) => {
      this.fileTypeForm.viewName = data.name;
      this.fileTypeForm.titleLine = data.titleLine;
      this.fileTypeForm.linkId = data.linkId;
      this.fileTypeForm.path = data.path;
      this.fileTypeForm.startLine = data.startLine;
      this.fileTypeForm.endLine = data.endLine;
      this.fileTypeForm.sheetName = data.sheetName;
      this.fileTypeForm.dictFlag = data.dictFlag;
      if (data.queryParams) {
        this.parameterTable = data.queryParams;
      }
      this.fieldTable = data.fields;
    });
  }
  //  
  addSure() {
    const fileTypeForm: any = this.$refs.fileTypeForm;
    fileTypeForm.validate((valid: any) => {
      let checkFlag = false;
      if (valid) {
        this.fieldTable = this.$refs.treeLists['treeDataList'];
        console.log(' this.fieldTable', this.fieldTable);
        this.parameterTable = this.$refs.parameViewTable['parameterData'];
        checkFlag = statementChange(this.parameterTable, this.fieldTable);
        console.log('checkFlag', checkFlag);
        // this.parameterTable.forEach((key, index) => {
        //   if (
        //     !key.code &&
        //     !key.name &&
        //     !key.type &&
        //     !key.required &&
        //     !key.defaultValue
        //   ) {
        //     this.parameterTable.splice(index, 1);
        //   }
        // });
        if (checkFlag === false) {
          const params: any = {
            name: this.fileTypeForm.viewName,
            type: 0,
            kind: 2,
            dictFlag: this.fileTypeForm.dictFlag,
            linkId: this.fileTypeForm.linkId,
            path: this.fileTypeForm.path,
            fields: this.fieldTable,
            sheetName: this.fileTypeForm.sheetName,
            titleLine: this.fileTypeForm.titleLine,
            startLine: this.fileTypeForm.startLine,
            endLine: this.fileTypeForm.endLine
          };
          if (this.parameterTable.length > 0 && this.parameterTable[0].code) {
            params.queryParams = this.parameterTable;
          } else {
            params.queryParams = [];
          }
          if (
            (this.isCompatible === '0' && this.updateId) ||
            (this.isCompatible === '1' && this.updateId)
          ) {
            params.id = this.updateId;
            params.isCompatible = this.isCompatible;
            viewDataEdit(params).then(({ data, error }) => {
              if (!error) {
                this.$message.success('');
                fileTypeForm.resetFields();
                this.$router.back();
              } else {
                this.$message.error(':' + error.message);
              }
            });
          } else {
            dataViewAdd(params).then(({ data, error }) => {
              if (!error) {
                this.$message.success('');
                fileTypeForm.resetFields();
                this.$router.back();
              } else {
                this.$message.error(':' + error.message);
              }
            });
          }
        }
      }
    });
  }
}
